.btn {
    font-size: 1rem;
    font-weight: 400;
    outline: none;
    color: #2F312A;
    background: transparent;
    border: 0;
    border-radius: 2px;
    width: 100%;
    max-width: 200px;
    min-width: 100px;
    height: calc(1em*2 + 6px);
    margin-top: 5px;
    margin-bottom: 5px;
    box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.15);
    text-transform: uppercase;
    text-align: center;
    user-select: none;
    transition: background-color 0.5s ease;
}

.btn+.btn {
    margin-left: 5px;
}

.btn:hover {
    color: black;
    cursor: pointer;
}

.btn:active {
    transform: translateY(3px);
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
}

.btn:disabled,
.btn:hover:disabled {
    border: solid 1px #CCC;
    color: #CCC;
    background: transparent;
    box-shadow: none;
}

input {
    -webkit-appearance: none;
    outline: none;
    border-radius: 2px;
    border: 1px solid #aaa;
    margin-bottom: 5px;
    padding: 5px;
    font-size: 1rem;
    font-weight: lighter;
    color: black;
    background: white;
    margin-left: 5px;
}

input:disabled {
    background: #EEE;
    color: #444;
}

.flex-col {
    display: flex;
    flex-flow: column;
}

.flex-row {
    display: flex;
    flex-flow: row;
}

.flex-center {
    justify-content: center;
    align-items: center;
    text-align: center;
}

.flex-end {
    align-items: flex-end;
}

.flex-fill {
    flex: 1;
}

.flex-wrap {
    flex-wrap: wrap;
}

.flex-between {
    justify-content: space-between
}

.bookNowButton {
    font-size: 2rem;
    padding: 10px;
    color: white;
    background: var(--brand-colour3);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 4px 7px rgba(0, 0, 0, 0.15);
}

.bookNowButton:hover {
    cursor: pointer;
    box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.15);
}

.robotIcon {
    height: 24px;
    width: 24px;
}

.bookNowButton .robotIcon {
    margin-right: 10px;
}

.bookNowButton.inline {
    position: relative;
}

.bookNowButton.top-left {
    position: fixed;
    top: 20px;
    left: 20px;
}

.bookNowButton.top-right {
    position: fixed;
    top: 20px;
    right: 20px;
}

.bookNowButton.bottom-left {
    position: fixed;
    bottom: 20px;
    left: 20px;
}

.bookNowButton.bottom-right {
    position: fixed;
    bottom: 20px;
    right: 20px;
}

.bookChatContainer {
    margin-top: 80px;
    margin-right: 10px;
    padding: 2px;
    background: white;
    border-radius: 10px;
    box-shadow: 0px 4px 20px 1px rgba(0, 0, 0, 0.1);
    max-height: calc(100vh - 100px);
    width: 350px;
    max-width: 350px;
    display: flex;
    flex-direction: column;
    font-family: 'Lato', 'Assistant', sans-serif;
    position: fixed;
    right: 20px;
    top: 20px;
}

.bookChatContainer input[type="text"],
.bookChatContainer input[type="email"] {
    padding: 5px;
}

.bookChatHeader {
    padding: 10px 5px 10px 5px;
    margin: 5px;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.bookChatHeader .btn {
    font-size: 24px;
}

.bookChatAvatar {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    width: 40px;
    border-radius: 20px;
    background-color: var(--brand-colour3); 
    margin-right: 5px;
}

.bookChat {
    overflow: scroll;
    overflow-x: hidden;
    width: 100%;
    flex: 1;
}

.bookChatInfo {
    flex: 1;
    text-align: center;
    font-size: 12px;
    padding: 5px;
}

.bubbleTransition {
    display: flex;
    flex-direction: column;
}

.bookChat select {
    outline: none;
    border: 0;
    border-radius: 0;
    padding: 5px;
    max-height: 50px;
    margin: 5px;
    font-family: inherit;
    font-size: inherit;
    background: transparent;
    text-align: center;
}

.bookChat input {
    outline: none;
    border: 0;
    border-bottom: 1px dotted #AAA;
    border-radius: 0;
    background: transparent;
    color: black;
    padding: 5px;
    margin: 5px;
    font-family: inherit;
    font-size: inherit;
    text-align: center;
    width: 100%;
    max-width: 250px;
}

.bookChat input:disabled {
    border-bottom: none;
}

/* React transitions */
.bubbleTransition {
    display: flex;
}
.bubbleTransition-enter {
    opacity: 0.01;
}
.bubbleTransition-enter.bubbleTransition-enter-active {
    opacity: 1;
    transition: opacity 750ms ease-in;
}
.bubbleTransition-exit {
    opacity: 1;
}
.bubbleTransition-exit.bubbleTransition-exit-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
}

/* https://auralinna.blog/post/2017/how-to-make-a-css-speech-bubble-with-borders-and-drop-shadow */
.botMessageBubble {
    background-color: #EEE;
    color: black;
    border-radius: 2px;
    font-size: 16px;
    line-height: 1.3;
    margin: 10px 5px 20px 5px;
    padding: 15px;
    position: relative;
    text-align: left;
    margin-right: 35px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}

.botMessageBubble::after {
    border-right: 10px solid transparent;
    border-top: 10px solid #EEE;
    bottom: -10px;
    content: "";
    position: absolute;
    left: 10px;
}

.customerMessageBubble {
    background: var(--brand-colour3);
    color: white;
    border-radius: 2px;
    font-size: 16px;
    line-height: 1.3;
    margin: 10px 5px 20px 5px;
    padding: 10px;
    position: relative;
    max-width: 400px;
    margin-left: 35px;
    box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.1);
}

.customerMessageBubble::after {
    border-left: 10px solid transparent;
    border-top: 10px solid var(--brand-colour3);
    bottom: -10px;
    content: "";
    position: absolute;
    right: 10px;
}

.customerMessageBubbleEdit {
    background: white;
    color: black;
    border: 1px solid var(--brand-colour3);
    border-radius: 2px;
    font-size: 16px;
    line-height: 1.3;
    margin: 10px 5px 20px 5px;
    padding: 5px;
    position: relative;
    max-width: 400px;
    box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.1);
}

