    width: 20px;
    height: 20px;
    float: right;
    left: -8px;
    top: 0px;
    position: relative;
    z-index: 2;
    visibility: hidden;
    flex: none;
}

.customerMessageBubble:hover .customerMessageBubbleEditIcon {
    visibility: visible;
}

.sent .customerMessageBubble:hover .customerMessageBubbleEditIcon {
    visibility: hidden;
}

.customerMessageBubbleEdit::after {
    border-left: 10px solid transparent;
    border-top: 10px solid var(--brand-colour3);
    bottom: -10px;
    content: "";
    position: absolute;
    right: 10px;
}

.bookRecaptcha {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 10px 0px 10px 0px;
}

.bookNameEmail {
    width: 100%;
    padding: 0px 20px 20px 20px;
}

.btn-small {
    background: var(--brand-colour2);
    border: 1px solid var(--brand-colour2);
    color: #444;
    width: 40px;
    height: 40px;
    min-width: 0px;
    text-transform: none;
    box-shadow: none;
    border-radius: 20px;
    padding: unset;
    transition: background-color 0.5s ease, box-shadow 0.5s ease, border-color 0.5s ease;
}

.btn-small-highlight,
.btn-small:hover {
    background: white;
    border: 1px solid var(--brand-colour3);
    color: var(--brand-colour3);
    text-transform: none;
    box-shadow: none;
    border-radius: 20px;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.1);
}

.btn-small-pulse:disabled,
.btn-small:disabled {
    color: #CCC;
}

.btn-vsmall {
    background: white;
    border: 1px solid var(--brand-colour3);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
    color: var(--brand-colour3);
    width: 20px;
    height: 20px;
    min-width: 0px;
    text-transform: none;
    border-radius: 10px;
    font-size: 10px;
    text-align: center;
    padding: unset;
    transition: background-color 0.5s ease, box-shadow 0.5s ease, border-color 0.5s ease;
}

.btn-vsmall:active {
    transform: translateY(1px);
}

.btn-vsmall:hover {
    border-radius: 10px;
}

.btn-vsmall:disabled {
    background: var(--brand-colour2);
    border: 1px solid #CCC;
    color: #CCC;
    box-shadow: none;
}

.bookSelField {
    padding: 5px 10px 5px 10px;
    background-color: white;
    color: var(--brand-colour3);
    transition: background-color 0.5s ease, box-shadow 0.5s ease;
    border: 1px solid var(--brand-colour3);
    border-radius: 20px;
    min-width: 85px;
    margin: 3px;
    user-select: none;
    font-size: 16px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
}

.bookSelField:hover {
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.bookSelField:active {
    background-color: var(--brand-colour3);
    color: white;
}

.bookSelField-selected {
    background-color: var(--brand-colour3);
    color: white;
}

.bookSelField-selected:active {
    background-color: white;
    color: black;
}

.bookChatButtonRow {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.poweredBy {
    text-align: left;
    padding: 5px 10px;
    font-size: 60%;
    opacity: .7;
}

@media (max-width: 420px) {
    .bookChatContainer {
        margin: 0px;
        padding: 0px;
        border-radius: 0px;
        height: auto;
        width: 100vw;
        min-height: 100vh;
        max-height: 0;
        max-width: 100vw;
    }
    .bookChat {
        max-width: 100vw;
    }
    .bookContainerCard {
        max-width: 100vw;
    }
}
