    /* background-image is loaded and set dynamically */
    background-color: white;
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-attachment: fixed;
    background-position-x: center;
    margin: 0;
    padding: 0;
}

:root {
    /* Charcoal */
    --brand-colour1: #0C0D07;
    /* Salt */
    --brand-colour2: #F7F7F7;
    /* Ultramarine */
    --brand-colour3: #3E01F7;
    /* Wild Melon */
    --brand-colour4: #FE6F81;
    /* Contra */
    --brand-colour5: #D8FF72;
    /* Silica */
    --brand-colour6: #8AFCF9;
    /* Dragon */
    --brand-colour7: #A8FFB0;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

