.outdr-widget {
    font-family: 'Assistant', sans-serif;
    box-sizing: border-box;
    /* background-image is loaded and set dynamically */
    background-color: white;
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-attachment: fixed;
    background-position-x: center;
    margin: 0;
    padding: 0;
}

:root {
    /* Charcoal */
    --brand-colour1: #0C0D07;
    /* Salt */
    --brand-colour2: #F7F7F7;
    /* Ultramarine */
    --brand-colour3: #3E01F7;
    /* Wild Melon */
    --brand-colour4: #FE6F81;
    /* Contra */
    --brand-colour5: #D8FF72;
    /* Silica */
    --brand-colour6: #8AFCF9;
    /* Dragon */
    --brand-colour7: #A8FFB0;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

/* override some react-dates defaults */
.CalendarDay__selected,
.CalendarDay__selected:hover,
.CalendarDay__selected_span:hover {
    background: var(--brand-colour3);
    border: 1px double var(--brand-colour3);
    color: #fff;
}

.CalendarDay__selected_span {
    background: #8075FF;
    border: 1px double #8075FF;
    color: #fff;
}
